.reservation {
  width: 100%;
  background-color: $color-grey;
  background-image: url("../img/reservation-bck.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  margin-top: 120px;
  padding-top: 45px;
  padding-bottom: 50px;


  @media (min-resolution: 2dppx) {
    background-image: url("../img/reservation-bck@2x.jpg");
  }

  @media (max-width: $vp-1024) {
    background-image: url("../img/reservation-bck-tab.jpg");
    display: flex;
    flex-direction: column;
    margin-top: 100px;
    padding-bottom: 99px;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/reservation-bck-tab@2x.jpg");
    }
  }

  @media (max-width: $vp-768) {
    background-image: url("../img/reservation-bck-mob.jpg");
    margin-top: 64px;
    padding-top: 11px;
    padding-bottom: 71px;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/reservation-bck-mob@2x.jpg");
    }
  }
}

.reservation__container {
  margin: 0 auto;
  width: 1160px;
  display: grid;
  grid-template-columns: 1fr 1fr;

  @media (max-width: $vp-1024) {
    width: 678px;
    display: flex;
    flex-direction: column;
  }


  @media (max-width: $vp-768) {
    width: 320px;
  }

}
.reservation__description {
  width: 580px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  text-align: left;

  @media (max-width: $vp-1024) {
    align-items: center;
    text-align: center;
    padding-top: 45px;
  }

  @media (max-width: $vp-768) {
    width: 320px;
  }
}

.reservation__title {
  font-weight: 700;
  font-size: 40px;
  line-height: 60px;
  margin: 0;
  margin-left: 59px;
  margin-top: 27px;

  @media (max-width: $vp-1024) {
    margin: 0;
    margin-bottom: 0;
    font-size: 34px;
    line-height: 51px;
    margin-top: 4px;
  }

  @media (max-width: $vp-768) {
    font-weight: 700;
    font-size: 24px;
    line-height: 31px;
    width: 194px;
    margin-bottom: 12px;
  }

}

.reservation__paragraph {
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  margin-left: 59px;
  margin-bottom: 33px;
  width: 346px;

  @media (max-width: $vp-1024) {
    margin: 0;
    margin-bottom: 52px;
    width: 352px;
  }

  @media (max-width: $vp-768) {
    font-size: 16px;
    line-height: 22px;
    width: 205px;
    margin-bottom: 35px;
  }
}

.reservation__content {
  width: 403px;
  margin: 0 auto;
  background-color: $color-tangaroa;
  padding: 50px 50px;
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (max-width: $vp-1024) {
    padding-top: 45px;
  }

  @media (max-width: $vp-768) {
    width: 290px;
    padding: 32px;
  }
}

.reservation__feedback {
  width: 636px;

  @media (max-width: $vp-1024) {
    margin: 0 auto;
  }

  @media (max-width: $vp-768) {
    width: 290px;
  }
}


.reservation__feedback-paragraph {
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: $color-alice-blue;
  text-align: left;
  margin: 0;
  margin-bottom: 26px;

  @media (max-width: $vp-768) {
    text-align: center;
    line-height: 25px;
    margin-bottom: 32px;
  }
}

.reservation__form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.reservation__form-paragraph {
  margin: 0;
  padding: 0;
  margin-bottom: 20px;
}

.reservation__form-label {
  font-weight: 400;
  font-size: 10px;
  line-height: 13px;
  color: $color-alice-blue;

  @media (max-width: $vp-768) {
    width: 160px;
    text-align: left;
  }
}

.reservation__form-input--name,
.reservation__form-input--phone,
.reservation__form-input--email {
  width: 303px;

  @media (max-width: $vp-768) {
    width: 230px;
  }
}
.reservation__form-input {
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 15px;


}

.reservation__form-paragraph--checkbox {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  margin-bottom: 30px;

  @media (max-width: $vp-768) {
    margin-bottom: 24px;
  }
}

.reservation__form-label--checkbox {
  @media (max-width: $vp-768) {
    margin-left: 35px;
  }
}

.reservation__form-input--checkbox {
  width: 20px;

}

.reservation__form-input--checkbox +label::before {
  position: absolute;
  content: "";
  width: 18px;
  height: 18px;
  background-color: $color-alice-blue;
  top: 0;
  left: 0;

  @media (max-width: $vp-768) {
    top: 2px;
    left: 25px;
  }
}

.reservation__form-input--checkbox:checked + label::after {
  position: absolute;
  content: "";
  width: 10px;
  height: 10px;
  background-image: url("../img/svg/tick.svg");
  background-repeat: none;
  top: 0;
  left: 0;
}

.reservation__button {
  width: 303px;
  background-color: $color-alice-blue;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  padding-top: 15px;
  padding-bottom: 10px;
  margin-top: 5px;
  cursor: pointer;
  border: none;

  @media (max-width: $vp-768) {
    width: 230px;
    margin-top: 0;
  }
}
